import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { palette } from "../lib/styles/palette.js";
import instance from "lib/Request.js";
import { useNavigate } from "react-router-dom";
import LocationSelect from "components/modals/LocationSelect.jsx";

function RegisterPage() {
	//       
	//             
	//  

	const [email, setEmail] = useState("");
	const [password, setPassword] = useState("");
	const [passwordConfirm, setPasswordConfirm] = useState("");
	const [birth, setBirth] = useState("");
	const [phone, setPhone] = useState("");
	const [name, setName] = useState("");
	const [nickname, setNickname] = useState("");
	const [location, setLocation] = useState("");
	const [sigungu, setSigungu] = useState("");
	const [postcode, setPostcode] = useState("");
	const [isOpen, setIsOpen] = useState(false);
	const navigate = useNavigate();

	const emailHandler = (e) => {
		e.preventDefault();
		setEmail(e.target.value);
	};
	const passwordHandler = (e) => {
		e.preventDefault();
		setPassword(e.target.value);
	};
	const passwordConfirmHandler = (e) => {
		e.preventDefault();
		setPasswordConfirm(e.target.value);
	};

	const nameHandler = (e) => {
		e.preventDefault();
		setName(e.target.value);
	};

	const nicknameHandler = (e) => {
		e.preventDefault();
		setNickname(e.target.value);
	};

	const birthHandler = (e) => {
		e.preventDefault();
		setBirth(e.target.value);
	};
	const phoneHandler = (e) => {
		e.preventDefault();
		setPhone(e.target.value);
	};

	const submitHandler = (e) => {
		e.preventDefault();

		if (password !== passwordConfirm) {
			alert("pw dif");
			return;
		}

		instance({
			method: "post",
			url: "/signup/",
			data: {
				email: email,
				password: password,
				name: name,
				nickname: nickname,
				birth: birth,
				phone: phone,
				location: location,
				sigungu: sigungu,
				postcode: postcode,
			},
		}).then((res) => {
			if (res.status === 200) {
				const accessToken = res.data.Token;
				instance.defaults.headers.common[
					"Authorization"
				] = `Token ${accessToken}`;
				localStorage.setItem("token", accessToken);
				const like = res.data.Like;
				localStorage.setItem("like", like);
				const sgg = res.data.Sigungu;
				localStorage.setItem("sigungu", sgg);
				navigate("/");
			}
		});
	};

	const onKeyPress = (e) => {
		if (e.key === "Enter") {
			submitHandler();
		}
	};

	useEffect(() => {
		if (localStorage.getItem("token")) {
			navigate("/");
		}
	}, []);

	return (
		<RegisterLayout>
			<Wrapper>
				<Text>'^'</Text>
				<Form action="">
					<InputIndex></InputIndex>
					<Input
						type="email"
						placeholder=""
						onChange={emailHandler}
						onKeyPress={onKeyPress}
					></Input>
					<InputIndex></InputIndex>
					<Input
						type="password"
						onChange={passwordHandler}
						placeholder=""
						onKeyPress={onKeyPress}
					></Input>
					<Input
						style={{ marginTop: "20px" }}
						type="password"
						onChange={passwordConfirmHandler}
						placeholder=" "
						onKeyPress={onKeyPress}
					></Input>
					<InputIndex></InputIndex>
					<Input
						type="text"
						placeholder=""
						onChange={nicknameHandler}
						onKeyPress={onKeyPress}
					></Input>
					<InputIndex></InputIndex>
					<Input
						type="text"
						placeholder=""
						onChange={nameHandler}
						onKeyPress={onKeyPress}
					></Input>
					<InputIndex></InputIndex>
					<Input
						type="date"
						onChange={birthHandler}
						onKeyPress={onKeyPress}
					></Input>
					<InputIndex></InputIndex>
					<Input
						type="text"
						value={sigungu}
						onClick={() => setIsOpen(true)}
					></Input>
					{isOpen ? (
						<LocationSelect
							isOpen={isOpen}
							getIsOpen={(v) => setIsOpen(v)}
							getLocation={(v) => setLocation(v)}
							getSigungu={(v) => setSigungu(v)}
							getPostcode={(v) => setPostcode(v)}
						/>
					) : (
						<></>
					)}
					<InputIndex></InputIndex>
					<Input
						style={{ width: "460px" }}
						onChange={phoneHandler}
						type="text"
						onKeyPress={onKeyPress}
					></Input>
					<AuthrizePhone> </AuthrizePhone>
					<Submit onClick={submitHandler}></Submit>
				</Form>
			</Wrapper>
		</RegisterLayout>
	);
}

const RegisterLayout = styled.div`
	width: 1250px;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding-bottom: 100px;
`;

const Wrapper = styled.div`
	width: 700px;
	height: 970px;
	display: flex;
	flex-direction: column;
`;

const Text = styled.div`
	display: flex;
	font-size: 30px;
	width: 700px;
	height: 70px;
	margin-top: 70px;
	align-items: center;
	justify-content: center;
`;

const Form = styled.form`
	width: 700px;
	height: 680px;
`;

const InputIndex = styled.div`
	height: 25px;
	font-size: 15px;
	color: ${palette.MainColor};
	margin: 20px 0 0 50px;
	display: flex;
`;

const Input = styled.input`
	:focus {
		outline: 2px solid ${palette.MainColor};
	}

	:placehorder {
		font-size: 20px;
		color: #666666;
	}
	margin-left: 50px;
	width: 600px;
	height: 50px;
	font-size: 20px;
	padding-left: 10px;
	box-sizing: border-box;
	border: 1px solid ${palette.MainColor};
`;

const AuthrizePhone = styled.button`
	margin-left: 20px;
	width: 120px;
	height: 50px;
	background-color: ${palette.MainColor};
	color: white;
	font-size: 15px;
`;

const Submit = styled.button`
	margin: 50px 0 0 50px;
	width: 600px;
	height: 50px;
	font-size: 20px;
	color: white;
	background-color: ${palette.MainColor};
`;

export default RegisterPage;
